<script>
	import wPicker from "@/components/w-picker/w-picker.vue";

	export default {
		data() {
			return {
				cross: '',
				ro: {
					yonghuId: false,
					gerentizhengNum: false,
					tizhongNum: false,
					shentiTypes: false,
					meishiPhoto: false,
					gerentizhengContent: false,
					gerentizhengTime: false,
					gerentizhengDelete: false,
					insertTime: false,
					createTime: false,
				},
				ruleForm: {
					yonghuId: '',
					gerentizhengNum: '',
					tizhongNum: '',
					shentiTypes: '', //数字
					shentiValue: '', //数字对应的值
					meishiPhoto: '',
					gerentizhengContent: '',
					gerentizhengTime: '',
					gerentizhengDelete: '',
					insertTime: '',
					createTime: '',
				},
				// 登陆用户信息
				user: {},
				shentiTypesOptions: [],
				shentiTypesIndex: 0,

			}
		},
		components: {
			wPicker
		},
		computed: {
			baseUrl() {
				return this.$base.url;
			},
		},
		async onLoad(options) {

			/*下拉框*/
			let shentiTypesParams = {
				page: 1,
				limit: 100,
				dicCode: 'shenti_types',
			}
			let shentiTypes = await this.$api.page(`dictionary`, shentiTypesParams);
			this.shentiTypesOptions = shentiTypes.data.list


			// 如果是更新操作
			if (options.id) {
				this.ruleForm.id = options.id;
				// 获取信息
				let res = await this.$api.info(`gerentizheng`, this.ruleForm.id);
				this.ruleForm = res.data;
			}
			if (options.gerentizhengId) {
				this.ruleForm.gerentizhengId = options.gerentizhengId;
			}
			// 跨表
			// this.styleChange()
		},
		methods: {
			// 下拉变化
			shentiTypesChange(e) {
				this.shentiTypesIndex = e.target.value
				this.ruleForm.shentiValue = this.shentiTypesOptions[this.shentiTypesIndex].indexName
				this.ruleForm.shentiTypes = this.shentiTypesOptions[this.shentiTypesIndex].codeIndex
			},


			// styleChange() {
			// 	this.$nextTick(() => {
			// 		// document.querySelectorAll('.app-update-pv .cu-form-group .uni-yaoxianStyle-yaoxianStyle').forEach(el=>{
			// 		//   el.style.backgroundColor = this.addUpdateForm.yaoxianStyle.content.backgroundColor
			// 		// })
			// 	})
			// },


			meishiPhotoTap() {
				let _this = this;
				this.$api.upload(function(res) {
					_this.ruleForm.meishiPhoto = 'upload/' + res.file;
					_this.$forceUpdate();
					_this.$nextTick(() => {
						// _this.styleChange()
					})
				});
			},
			// 日期控件
			gerentizhengTimeConfirm(val) {
				this.ruleForm.gerentizhengTime = val.result;
				this.$forceUpdate();
			},
			// 日期控件
			insertTimeConfirm(val) {
				this.ruleForm.insertTime = val.result;
				this.$forceUpdate();
			},
			// 日期控件
			createTimeConfirm(val) {
				this.ruleForm.createTime = val.result;
				this.$forceUpdate();
			},




			getUUID() {
				return new Date().getTime();
			},
			async onSubmitTap() {
				if ((!this.ruleForm.gerentizhengNum) && (!this.$validate.isNumber(this.ruleForm.gerentizhengNum)) &&
					this.ruleForm.gerentizhengNum > 0) {
					this.$utils.msg(`当前身高不能为空，不能小于0 格式为数字或小数`);
					return
				}
				if ((!this.ruleForm.tizhongNum) && (!this.$validate.isNumber(this.ruleForm.tizhongNum)) && this
					.ruleForm.tizhongNum > 0) {
					this.$utils.msg(`当前体重不能为空，不能小于0 格式为数字或小数`);
					return
				}
				if ((!this.ruleForm.gerentizhengTime)) {
					this.$utils.msg(`下次体检时间不能为空`);
					return
				}
				if (this.ruleForm.id) {
					await this.$api.update(`gerentizheng`, this.ruleForm);
				} else {
					await this.$api.save(`gerentizheng`, this.ruleForm);
				}
				uni.setStorageSync('pingluenStateState', true);
				this.$utils.msgBack('提交成功');
			},
			getDate(type) {
				const date = new Date();
				let year = date.getFullYear();
				let month = date.getMonth() + 1;
				let day = date.getDate();
				if (type === 'start') {
					year = year - 60;
				} else if (type === 'end') {
					year = year + 2;
				}
				month = month > 9 ? month : '0' + month;;
				day = day > 9 ? day : '0' + day;
				return `${year}-${month}-${day}`;
			},
			toggleTab(str) {
				this.$refs[str].show();
			}
		}
	}
</script>
